import React, { Component } from 'react'
import styled from "styled-components";
import {Link} from 'react-router-dom';
export default class Product extends Component {
    render() {
        return (
            <ProductWrapper className='col-9 ma-auto col-md-6 col-lg-3 my-3'>
                <div className='card'>
                    <div className='img-container p-5' onClick={()=>console.log("clicked")}>
                        <Link to="/details"> 
                        <img src={this.props.value.img} className="card-img-top" ></img>
                        </Link>
                    <button className="cart-btn" onClick={()=>console.log('button')} disabled={this.props.value.inCart}>
                    {
                       this.props.value.inCart?<p className='text-capitalize mb-0'>In Cart</p>:<i className='fas fa-cart-plus' />
                    }
                    </button>
               </div>
               <div className="card-footer d-flex justify-content-between">
                   <p className="align-self-center mb-0">
                       {this.props.value.title}
                   </p>
                   <h5 className="text-blue font-italic mb-0">
                        <span className="mr-1">
                           â‚¹ {this.props.value.price}
                        </span>
                   </h5>
               </div>
               </div>
                
            </ProductWrapper>
        )
    }
}
const ProductWrapper =styled.div`
.card{
    border-color:transperant;
    transition:all 0.5s linear; 
}
.card-footer{
    background:transperent;
    border-top:transpperent;
    transition:all 1s linear; 

}
&:hover{
    .card{
        border:0.04rem solid rgba(0,0,0,0.2);
        box-shadow:2px 2px 5px 0px rgba(0,0,0,0.2);
    }
    .card-footer{
        background:regba(247,247,247);
    }
}
.img-container{
    position:relative;
    overflow:hidden;
}
.card-img-top{
    transition:all 0.5s linear;
}
.img-container:hover .card-img-top{
   transform:scale(1.2); 
}
.cart-btn{
    position:absolute;
    bottom:0;
    right:0;
    padding:0.2rem 04rem;
    background:var(--lightBlue);
    border:none;
    color:var(--mainWhite);
    font-size:1.4rem;
    border-radius:0.5rem 0 0 0;
    transform:translate(100%,100%);
    transition:all 0.5s linear;
    
}
.img-container:hover .cart-btn{
    transform:translate(0,0);

}
.cart-btn:hover{
    color:var(--mainBlue);
}
`;
